*{
 margin: 0;
 padding: 0; 
 height: 100%;
 width: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.StuLogin{
    background-color: #0C0E2E;
}

.StatelessName{
    display: block;
    width: 100%;
    height: 100px;
    font-family: 'Geo';
    color: white;
    font-size: 45pt;  
    text-align: end;
    padding: 20px 20px 0 0;
    float: right;
    margin-bottom: 60px;
    transition: ease-out 1s;
}

.StatelessName:hover{
    color: #FF7474;
}

.StuLogBoxes{
    padding: 10px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 25%;
    align-items: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.textBoxInput{
    border-style: solid;
    border-width: thin thin medium thin;
    padding: 10px;
    color: white;
    font-size: 13pt;
    background-color: #0C0E2E;
    border-radius: 5px 5px 0 0;
    border-color: white;
    margin-top: 5px;
    margin-bottom: 20px;
}

.labeler{
     color: #FF7474;
     font-family: 'Mulish';
     font-weight: bold;
}

.StuLogButton{
    background-color: #FF7474;
    color: #0C0E2E;
    font-weight: bolder;
    letter-spacing: 2px;
    text-transform: uppercase;
    border: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 10px 20px 10px 20px;
    font-family: 'Mulish';
    margin-left: 57.5%;
    margin-top: 10px;
    border-radius: 5px;
    transition: ease-in-out 0.2s;
}

.StuLogButton:hover{
    color: white;
    border-radius: 7px;
}

.textBoxInput:hover{
    border-color: white white #FF7474 white;
}

.NavigateTo{
    color: white;
    font-family: 'Mulish';
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    margin-top: 20px;
    text-align: center;
    font-weight: bold;
}

.NavigateTo:hover{
    color: tomato;
}

@media only screen and (max-width: 900px){
    .StatelessName{
        display: unset;
        font-size: 50pt;
    }
}
